.flex-page .at-row {
  margin-bottom: 0.85333rem;
}

.flex-page .at-row .at-col {
  padding: 0.384rem 0.34133rem;
  color: #FFF;
  font-size: 0.59733rem;
  text-align: center;
}

.flex-page .at-row .at-col:nth-child(odd) {
  background-color: #78A4F4;
}

